<template>
  <div class="index">
    <div class="container">
      <div class="row">
        <div class="s8 col offset-s2">
          <p>Gamepro5 is a Gaming YouTuber and Programer. He makes gaming YouTube videos and makes websites. He loves technology and computers and knows a lot about them. He knows HTML, some CSS, and some JavaScript (Blocks). He plays Overwatch professionally and also plays Hearthstone, Clash Royale, and Heroes of the Storm. His goal is to make entertaining YouTube videos and to make his site even better! </p>
          <b>Status Update:</b> Procrastinating
        </div>
      </div>
      <div class="row center">
        <h1>Quick Links</h1>
        <span v-for="(ql,index) in quicklink" :key="index">
          <a :href="ql.href"><img :src="ql.src" width="48px" height="48px" class="ico"></a>
        </span>
      </div>
      <div class="row center">
        <iframe width="850" height="480" src="https://www.youtube.com/embed/LEq8wDuk9Yg" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      quicklink: [
        {
          href: "https://www.youtube.com/c/gamepro5",
          src: "https://gamepro5.github.io/images/youtube.svg"
        },
        {
          href: "https://plus.google.com/+Gamepro5",
          src: "https://gamepro5.github.io/images/googleplus.svg"
        },
        {
          href: "https://steamcommunity.com/id/Gamepro5",
          src: "https://gamepro5.github.io/images/steam.svg"
        },
        {
          href: "https://twitter.com/gamepro543",
          src: "https://gamepro5.github.io/images/twitter.svg"
        },
        {
          href: "https://github.com/Gamepro5",
          src: "https://gamepro5.github.io/images/github.svg"
        },
        {
          href: "https://discord.gg/j2YzpvC",
          src: "https://gamepro5.github.io/images/discord.svg"
        },
        {
          href: "https://playoverwatch.com/en-us/career/pc/Gamepro5-1279",
          src: "https://gamepro5.github.io/images/overwatch.svg"
        }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.ico {
  margin: 10px;
  opacity: 0.6;
}
.ico:hover {
  opacity: 1;
}
a{
  color:#F44336;
}
</style>



