<template>
    <div class="programs container">
        <div class="row center">
            <h4>Games By GamePro5</h4>
        </div>
        <div class="row center" v-if="game">
            <h4>{{curgame.name}}</h4>
            <iframe :src="curgame.href" height="500" width="600"></iframe>
        </div>
        <div class="row">
            <div class="col s4" v-for="(game,index) in programs" :key="index">
                <router-link class="red-text" :to="{name:'Programs',params:{game:game.name}}">Play {{game.name}}</router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "Programs",
  data() {
    return {
      programs: [
        {
          name: "Duel Cubes",
          href:
            "https://snap.berkeley.edu/snapsource/snap.html#present:Username=gamepro5&ProjectName=Duel%20Cubes&hideControls"
        },
        {
          name: "Pong Game",
          href:
            "https://snap.berkeley.edu/snapsource/snap.html#present:Username=Gamepro5&ProjectName=Pong%20Game&hideControls"
        },
        {
          name: "The Fortune Squirrel",
          href:
            "https://snap.berkeley.edu/snapsource/snap.html#present:Username=Gamepro5&ProjectName=The%20Fortune%20Squirrel&hideControls"
        },
        {
          name: "Guess My Number",
          href:
            "https://snap.berkeley.edu/snapsource/snap.html#present:Username=Gamepro5&ProjectName=Guess%20My%20Number%20Game&hideControls"
        },
        {
          name: "Modern Art With Polygons",
          href: ""
        },
        {
          name: "Duel Cubes",
          href: ""
        },
        {
          name: "Contact List Project",
          href: ""
        },
        {
          name: "Bar Graph Project",
          href: ""
        },
        {
          name: "Musical Lists",
          href: ""
        },
        {
          name: "Additive Factorial Algorithms",
          href: ""
        },
        {
          name: "Disease Spread Simulator",
          href: ""
        },
        {
          name: "Binary Calculator",
          href: ""
        },
        {
          name: "Recursive Polygons",
          href: ""
        }
      ],
      game: this.$route.params.game,
      curgame: { name: "", href: "" }
    };
  },
  watch: {
    $route: "updateGame"
  },
  methods: {
    updateGame() {
      this.game = this.$route.params.game;
      this.programs.forEach(program => {
        if (program.name == this.game) {
          this.curgame = program;
        }
      });
    }
  },
  created() {
    this.updateGame();
  }
};
</script>

<style>
</style>





