<template>
    <div class="media container">
        <div class="row center">
            <h4>The One Image Gamepro5 Likes</h4>
        </div>
        <div class="row center">
            <img src="https://gamepro5.github.io/images/adorableturtle.png" alt="tiny turtle struggleing to eat a strawberry"><br>
            Here is an image of an adorable tiny turtle struggling to eat a strawberry
        </div>
        <div class="row center">
            <h4>Music Gamepro5 Likes</h4>
            <p>These are some of my favorite songs. I do not own these, but I really enjoy them and want to share them with you for free here.</p>
        </div>
        <div class="row audio-container">
            <div class="card" v-for="(m,index) in musics" :key="index">
                <div class="card-content">
                <h5>{{m.name}}</h5>
                <br>
                <audio controls="">
                    <source :src="m.source" type="audio/mpeg">
                </audio>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "Media",
  data() {
    return {
      musics: [
        {
          name: "Ignite - Alan Walker",
          source: "https://gamepro5.github.io/music/Ignite-Alan_Walker.mp3"
        },
        {
          name: "Alone - Marshmello",
          source: "https://gamepro5.github.io/music/Alone-Marshmello.mp3"
        },
        {
          name: "Puzzle VIP - RetroVision",
          source: "https://gamepro5.github.io/music/Puzzle_VIP.mp3"
        },
        {
          name: "Double D - Debris & Dazers",
          source: "https://gamepro5.github.io/music/Double_D.mp3"
        },{
            name:"Gamepro5 - Screaming in an Autistic Manner",
            source:"https://gamepro5.github.io/music/gamepro5_screaming_in_an_autistic_manner.mp3"
        }
      ]
    };
  }
};
</script>

<style>
.audio-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-top: 60px;
}
</style>




